.fet{
	background-image:  linear-gradient(to right,rgba(0,0,0,0.4), rgba(255, 255, 255,0.2)), 
	url(https://images.unsplash.com/photo-1554415707-6e8cfc93fe23?ixlib=rb-1.2.1&ixid=eyJhcHBfaWQiOjEyMDd9&auto=format&fit=crop&w=1500&q=80);
	background-repeat: no-repeat;
	background-size: cover;
	top: 0;
    left: 0;
    width: 100%;
    height: auto;
}
.crd{
	background:  linear-gradient(to right,rgba(0,0,0,0.4), rgba(128, 0, 128,0.4), rgba(255, 0, 255,0.3),rgba(255, 128, 255,0.3));
}

.titl{
