
.list{
	padding: 0;
	margin: 0;
}

#slider-wrapper{
	max-width: 70%;
  	width: auto;
	height: auto;
	margin: 50px auto;
	background-image: url(https://images.unsplash.com/photo-1516541196182-6bdb0516ed27?ixlib=rb-1.2.1&ixid=eyJhcHBfaWQiOjEyMDd9&auto=format&fit=crop&w=1534&q=80);
	padding: 15px;	
}
#image-slider{
	width: auto;
	height: auto;
	position: relative;
	overflow: hidden;
	margin: auto;
	display: block;
}
#image-slider ul{
	width: 100%;
	height: 400px;
	position: relative;
}
#image-slider ul li{
	display: inline-block;
	position: absolute;
	top: 0;
	left: 100%;
  	width: 100%;
  	height:100%; 
}
#image-slider ul li img {
	width: 50%;
  	height:90%;
  	opacity: 0.8;
  	padding-left: 20%;
}
@media(max-width: 600px){
	#image-slider ul li img{
	width:100%;
	padding-left: 20px;
}
}
#image-slider ul li.active-img{
	left: 0;
}
.next-img{
	z-index: 9999;
}
#thumbnail {
	cursor: default;
	margin-top: 3px;
	height: auto;
	display: table;	
	width: 100%;
}
#thumbnail ul {
	width: 100%;
	height: 100%;
}
#thumbnail li{
	list-style: none;
	float: left;
	cursor: pointer;
	min-width: 1px;
}																
#thumbnail li img{
	padding: 0;
	float: left;
	margin: 0px 1.5px;
	max-height: 56px;
	height: 56px;
}
#thumbnail li:first-child img{
	margin-right:0px;
  	margin-left:0px;
}	